@use 'variables'

header
    max-height: 557px

#precontent
    margin-bottom: .2em
    padding-bottom: 0
    #toc-container
        display: table
        border: 2px solid #a2a9b1
        padding: 1em 0
        font-size: 95%
        background-color: #f8f9fa
        max-width: 100%
        margin-top: 30px
        h2
            margin-top: .5em
            font-size: 1.5em
            margin: 0 1em
        #toc-container__inner
            @media (min-width: variables.$large-mobile) and (min-height: 962px)
                overflow-y: auto
                max-height: 225px
        nav
            margin: .5em 1.5em
        ol
            margin-bottom: 12px
        ol ol
            margin-bottom: 10px
        @media (max-width: variables.$mobile-width)
            margin-top: 20px
        @media (min-width: variables.$large-mobile) and (min-height: 962px)
            position: fixed
            top: 490px
            left: 7px
            width: calc(195px + 3em)
            margin-top: 0
            max-height: 278px
            overflow: hidden
            z-index: 100
    #post-description
        padding: 10px 14px
        padding-top: 20px
        border-bottom: 2px white solid
        margin-bottom: 2px
    figure.preview-image
        border: 1px outset #ccc
        margin-top: 18px
        padding: 6px 0px 2px
        img
            max-width: 100%
            max-height: 420px
            margin: 0 auto
            padding: 0
            margin-bottom: 6px
            border: 0
            display: block
        figcaption
            display: block
            margin: 0 8px
#maincontent
    h2
        margin-top: 20px

.pagination
    margin-bottom: 60px

.social-icons-block
    text-align: center

#taglist
    display: block
    width: 100%
    position: relative
    float: left
    ul
        display: block
        position: relative
        float: left
        margin-bottom: 12px
        padding-left: 10px
        li
            font-size: 10px
            line-height: 12px
            height: 12px
            display: block
            float: left
            margin: 3px 0
            padding: 0
            &::after, &:first-of-type::before
                content: "|"
                font-size: 11px
                font-weight: bold
                margin: 0 3px
            &:first-of-type::before
                margin: 0
                margin-right: 3px
        a
            display: inline
    @media (min-width: variables.$large-mobile) and (max-height: 575px)
        display: none
footer
    @media (min-width: variables.$large-mobile) and (max-height: 522px)
        display: none
@media (prefers-color-scheme: dark)
    #taglist ul li
        font-size: 11px
        a
            color: #2a7ae2
